___CSS_LOADER_EXPORT___.push([module.id, `@font-face {
  font-family: 'Abel';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

:root {
  --background-gradient-1: #95498d;
  --background-gradient-2: #aebbff;
  --alt-background-gradient-1: #7d3065;
  --alt-background-gradient-2: #ffaeae;
  --panel-color: rgba(0, 0, 0, 0.2);

  --grid-color: white;
  --grid-ship-color: white;
  --grid-miss-color: rgba(0, 0, 0, 0.5);
  --grid-hit-color: rgba(255, 0, 0, 0.5);
  --grid-sunk-color: rgba(255, 0, 0, 0.8);

  --text-color: white;
  --text-color-faded: rgba(255, 255, 255, 0.5);

  --button-background-color: transparent;
  --button-line-color: white;

  font-family: 'Abel', sans-serif;
}

input,
select,
textarea,
button {
  font-family: inherit;
  color: inherit;
  font-size: 1rem;
}

.drop-shadow-text {
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.32);
}

::selection {
  background-color: var(--panel-color);
}

.hidden {
  display: none;
}

.selectable:after,
button:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.selectable:hover:after {
  background-color: rgba(255, 255, 255, 0.5);
}

button:hover:after {
  background-color: rgba(255, 255, 255, 0.25);
}

.selectable:active:after,
button:active:after {
  background-color: rgba(0, 0, 0, 0.2);
}

button {
  padding: 5px 15px;
  background-color: transparent;
  border: 4px solid var(--button-line-color);
  border-bottom: 8px solid var(--button-line-color);
  color: var(--button-line-color);
  font-size: 1.8rem;
  position: relative;
  cursor: pointer;
}

button:active {
  border-bottom: 4px solid var(--button-line-color);
  border-top: 8px solid var(--button-line-color);
}
