___CSS_LOADER_EXPORT___.push([module.id, `body {
  background: linear-gradient(0, var(--background-gradient-1) 0%, var(--background-gradient-2) 100%);
  color: var(--text-color);

  min-height: 100vh;
  margin: 0;
  display: flex;
}

body.alt-color {
  background: linear-gradient(0, var(--alt-background-gradient-1) 0%, var(--alt-background-gradient-2) 100%);
}

.content-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.button-container {
  margin-top: 20px;
  display: flex;
  gap: 20px;
}

.sidebar {
  width: 350px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--panel-color);
}

.title {
  font-size: 3rem;
  margin-top: 5px;
}

.author {
  font-size: 1.2rem;
}

.game-log-title {
  font-size: 2rem;
  margin-top: 25px;
}

.game-log {
  font-size: 1.2rem;
  background-color: var(--panel-color);
  align-self: stretch;
  margin: 10px;
  padding: 10px;
  flex: 1;
  overflow-y: scroll;
}

.game-log div:not(:last-child) {
  color: var(--text-color-faded);
}
